export interface Transaction {
  id: string;
  type: 'income' | 'expense';
  amount: number;
  date: string;
  category: string;
  description?: string;
}

const STORAGE_KEY = 'transactions';

export function getTransactions(): Transaction[] {
  try {
    if (typeof window === 'undefined') return [];
    const data = localStorage.getItem(STORAGE_KEY);
    return data ? JSON.parse(data) : [];
  } catch (error) {
    console.error('Error reading transactions from storage:', error);
    return [];
  }
}

export function saveTransactions(transactions: Transaction[]): void {
  try {
    if (typeof window === 'undefined') return;
    localStorage.setItem(STORAGE_KEY, JSON.stringify(transactions));
  } catch (error) {
    console.error('Error saving transactions:', error);
  }
}

export function addTransaction(newTransaction: Omit<Transaction, 'id'>): Transaction {
  const transactions = getTransactions();
  const transaction: Transaction = {
    ...newTransaction,
    id: Date.now().toString(),
  };
  transactions.push(transaction);
  saveTransactions(transactions);
  return transaction;
}

export function deleteTransaction(id: string): void {
  let transactions = getTransactions();
  transactions = transactions.filter((tx) => tx.id !== id);
  saveTransactions(transactions);
}

export function updateTransaction(id: string, updatedData: Partial<Omit<Transaction, 'id'>>): void {
  let transactions = getTransactions();
  const index = transactions.findIndex((tx) => tx.id === id);
  if (index !== -1) {
    transactions[index] = { ...transactions[index], ...updatedData };
    saveTransactions(transactions);
  }
}
