"use client";

import { useState, useEffect } from 'react';
import { Transaction, getTransactions, addTransaction as addTx, deleteTransaction as deleteTx } from '@/lib/transactions';

export function useTransactions() {
  const [transactions, setTransactions] = useState<Transaction[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Initialize transactions from localStorage
    try {
      const savedTransactions = getTransactions();
      setTransactions(savedTransactions);
    } catch (error) {
      console.error('Error loading transactions:', error);
    } finally {
      setLoading(false);
    }
  }, []);

  const addTransaction = (data: Omit<Transaction, 'id'>) => {
    try {
      const newTx = addTx(data);
      setTransactions((prev) => [...prev, newTx]);
      return newTx;
    } catch (error) {
      console.error('Error adding transaction:', error);
      throw error;
    }
  };

  const deleteTransaction = (id: string) => {
    try {
      deleteTx(id);
      setTransactions((prev) => prev.filter((tx) => tx.id !== id));
    } catch (error) {
      console.error('Error deleting transaction:', error);
      throw error;
    }
  };

  // Calculate summary statistics
  const summary = {
    totalIncome: transactions
      .filter((tx) => tx.type === 'income')
      .reduce((acc, tx) => acc + tx.amount, 0),
    totalExpense: transactions
      .filter((tx) => tx.type === 'expense')
      .reduce((acc, tx) => acc + tx.amount, 0),
    balance: 0,
  };
  
  summary.balance = summary.totalIncome - summary.totalExpense;

  return { 
    transactions, 
    addTransaction, 
    deleteTransaction, 
    summary,
    loading 
  };
}
